/* ProjectDisplay.css */

.display {
    background-color: rgb(237, 148, 148);
    display: flex;
    flex-direction: column; /* Arrange children vertically */
    padding: 20px;
    color: white;
    font-family: Georgia, 'Times New Roman', Times, serif;
    align-items: left;
    padding-left: 60px;
    text-align: left; /* Center text horizontally */
}

.project {
    display: flex;
}

.project_title {
    color: white;
    font-size: 24px;
    padding: 20px;
    border-bottom: 1px solid rgb(247, 247, 247); /* Customize your border as you want */
    font-weight: bold !important;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.project_image {
    max-width: 70%;
    transition: transform 0.1s; /* Add a smooth transition effect */
}

.project_image:hover {
    transform: scale(1.1);
}

.project-description{
    padding-left: 20px;
    max-width: 50%;
}
